/*display/position等会影响布局的属性写在前面,宽高也写在前面一些,这些都是会触发reflow,并且是不可继承的属性都会放前面一些,字体颜色等可继承的放在后面,这是css的书写规范*/
#app .tab {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
/*border-bottom:1px solid rgba(7,17,27,.1)*/
  position: relative;
}
#app .tab:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: " ";
}
#app .tab .tab-item {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
}
#app .tab .tab-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
#app .tab .tab-item > a.active {
  color: #f01414;
}


